.header {
  height: 75px;
  background-color: #262c33;
  margin-bottom: 5em;
  box-shadow: 0px 2px 10px -1px #262c33;
}

.dashboard-name {
  font-weight: normal;
  letter-spacing: 0.05em;
}

.dashboard-container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 2em;
  padding-left: 2em;
  max-width: 930px;
}

.table {
  display: table;
  width: 100%;
  border-collapse: separate;
  border-spacing: 0px 10px;
}

.first-cell {
  text-align: center;
}

.table-row {
  display: table-row;
  background: white;
}

.row-shadow {
  box-shadow: 3px 3px 3px -1px #ccc;
}

.add-device-button {
  float: right;
  text-align: center;
  cursor: pointer;
  background-color: #8ff34a;
  padding: 0.5em 3em 0.4em 2em;
  font-size: 0.9em;
}

.table-column-header {
  font-size: 0.7em;
  display: table-cell;
  text-transform: uppercase;
  color: #828282;
  text-align: center;
}

.header-row {
  background: transparent;
}

.table-cell {
  display: table-cell;
  vertical-align: middle;
  padding-top: 1em;
  padding-bottom: 0.8em;
  text-align: center;
}

.table-cell:last-child {
  width: 100px;
  padding-right: 1em;
  text-align: right;
}

.device-action {
  color: #828282;
  border: none;
  cursor: pointer;
  background-repeat: no-repeat;
  background-size: 20px;
  background-position: top center;
  height: 30px;
  min-width: 48px;
  padding-top: 23px;
  font-size: 0.75em;
  text-decoration: none;
}

.edit-device-button {
  background-image: url(./img/edit.svg);
}

.delete-device-button {
  background-image: url(./img/delete.svg);
}

.modal-wrap {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: #e9e9e990;
}

.modal {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  background-color: white;
  align-content: center;
  justify-content: center;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  max-width: 390px;
  min-height: 150px;
}

.modal-text {
  width: 100%;
  text-align: center;
  margin-bottom: 1.6em;
}

.button {
  border: none;
  cursor: pointer;
  min-width: 100px;
  min-height: 30px;
  font-size: 0.8em;
  letter-spacing: 0.03em;
  border-radius: 3px;
}

.button-secondary {
  background-color: #dddcdc;
  margin-right: 2.5em;
}

.button-primary {
  background-color: #8ff34a;;
}